import Vue from 'vue'
import App from './App.vue'
import VueRouter from "vue-router"
import router from "./router"
// 引入sdk
import 'aliyun-webrtc-sdk'
// 引入vant
import "./plugins/vant"

Vue.config.productionTip = false

Vue.use(VueRouter)

new Vue({
  render: h => h(App),
  router,
  beforeCreate() {
    /**
   * 创始音视频实例,丢在全局$aliWebrtc上  自动推流, 自动订阅
   */
    Vue.prototype.$aliWebrtc = new window.AliRtcEngine({autoPublish: true, autoSubscribe: true});
  }
}).$mount('#app')
