<template>
  <div class="login flex">
    <div class="main">
      <div class="main-title">
        <span>音视频通信</span>
      </div>
      <van-form>
        <van-field
          v-model.trim="userName"
          name="用户名"
          label="用户名"
          placeholder="用户名"
          :rules="[{ required: false, message: '请填写用户名' }]"
        />
        <van-field
          v-model.trim="channelId"
          name="房间号"
          label="房间号"
          placeholder="房间号"
          :rules="[{ required: true, message: '请填写房间号' }]"
        />
        <div style="margin: 16px">
          <van-button
            type="info"
            block
            :disabled="channelId === '' || !isSupportRtc"
            @click="handleClickJoinBtn"
            >进入房间</van-button
          >
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
// 引入计算token的方法
import sha256 from '../assets/js/sha256'
// 引入vant方法
import { Toast } from 'vant'

export default {
  name: 'Login',
  data() {
    return {
      isSupportRtc: false,
      userName: Math.random()
        .toString(36)
        .replace(/[^a-z]+/g, '')
        .substr(0, 5), // 默认随机用户名
      channelId: '', // 房间号
      userId: '' // 用户号
    }
  },
  methods: {
    // 点击进入房间
    handleClickJoinBtn() {
      // 提示加入房间中
      Toast.loading({
        message: '加入房间...',
        forbidClick: true // 禁用背景点击
      })
      // 获取授权信息
      const authInfo = this.getAuthInfo()
      // 加入房间
      this.joinChannel(authInfo)
    },
    joinChannel(authInfo) {
      this.$aliWebrtc.joinChannel(authInfo, this.userName).then(
        () => {
          // 加入房间成功
          Toast.success('加入房间成功!');
          // 页面跳转
          this.$router.push({
            path: '/metting',
            query: {
              channelId: this.channelId,
              userName: this.userName,
              userId: this.userId
            }
          })
        },
        (error) => {
          // 加入房间失败
          Toast.fail('加入房间失败: ' + error.message);
        }
      )
    },
    getAuthInfo() {
      const appId = 'hil6ydzc' // 修改为自己的appid 该方案仅为开发测试使用，正式上线需要使用服务端的AppServer
      const key = '8172773aeb6531f8fb9efd5162e6a08c' // 修改为自己的key 该方案仅为开发测试使用，正式上线需要使用服务端的AppServer
      const userId = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(
        /[xy]/g,
        function (c) {
          var r = (Math.random() * 16) | 0,
            v = c == 'x' ? r : (r & 0x3) | 0x8
          return v.toString(16)
        }
      ) // 可以自定义
      this.userId = userId
      const timestamp = parseInt(new Date().getTime() / 1000 + 48 * 60 * 60)
      const nonce = 'AK-' + timestamp
      const token = sha256(
        appId + key + this.channelId + userId + nonce + timestamp
      )
      return {
        appid: appId,
        userid: userId,
        timestamp: timestamp,
        nonce: nonce,
        token: token,
        gslb: ['https://rgslb.rtc.aliyuncs.com'],
        channel: this.channelId
      }
    },
    checkSupportRtc() {
      this.$aliWebrtc
        .isSupport()
        .then((res) => {
          //支持webrtc
          console.log(res)
          this.isSupportRtc = true
        })
        .catch((error) => {
          //不支持webrtc
          alert('support err: ' + error.message)
          this.isSupportRtc = false
        })
    }
  },
  created() {
    // 检测是否支持webrtc
    this.checkSupportRtc()
  }
}
</script>

<style lang="less" scoped>
@import url('../assets/css/variable.less');
.login {
  height: 100vh;
  padding: 0 20px;
  background-color: @color_theme;
  &.flex {
    align-items: center;
    justify-content: center;
  }
  .main {
    width: 420px;
    max-width: 100%;
    background-color: #fff;
    padding: 30px 26px 24px 26px;
    font-size: 14px;
    .main-title {
      font-size: 30px;
      text-align: center;
      letter-spacing: 1px;
    }
  }
}
</style>